/* 메인 컨테이너 */
.main-container {
  display: flex;
  min-height: calc(100vh - 64px);
  background-color: var(--bg-secondary);
}

/* 메인 컨텐츠 영역 */
.store-main-content {
  flex: 1;
  overflow-y: auto;
  transition: margin-left 0.3s ease;

  &.menu-open {
    margin-left: 0;
  }
}

.checkout-container {
  min-height: 100vh;
  background-color: var(--bg-secondary);
}

/* 헤더 제거 (app-store-layout-header 사용) */

/* 메인 컨텐츠 */
.checkout-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 2rem;
  align-items: start;
}

/* 섹션 공통 스타일 */
.section {
  background-color: var(--bg-primary);
  border-radius: 8px;
  padding: 2rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 2px 4px var(--shadow);

  h2 {
    margin: 0 0 1.5rem 0;
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--border-color);
  }
}

/* 주문 상품 목록 */
.order-items {
  .item-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .order-item {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    transition: background-color 0.2s;

    &:hover {
      background-color: var(--bg-secondary);
    }

    .item-image {
      width: 80px;
      height: 80px;
      object-fit: cover;
      border-radius: 8px;
      background-color: var(--bg-secondary);
    }

    .item-info {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 0.25rem;

      .item-store {
        font-size: 0.85rem;
        color: var(--text-secondary);
      }

      .item-name {
        font-size: 1rem;
        font-weight: 500;
        color: var(--text-primary);
      }

      .item-quantity {
        font-size: 0.9rem;
        color: var(--text-secondary);
      }

      .item-price {
        font-size: 1.1rem;
        font-weight: 600;
        color: var(--accent-primary);
        margin-top: 0.25rem;
      }
    }
  }
}

/* 폼 스타일 */
form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  label {
    font-size: 0.95rem;
    font-weight: 500;
    color: var(--text-primary);
  }

  input,
  select {
    padding: 0.75rem;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    font-size: 0.95rem;
    background-color: var(--bg-primary);
    color: var(--text-primary);
    transition: border-color 0.2s;

    &:focus {
      outline: none;
      border-color: var(--accent-primary);
    }

    &::placeholder {
      color: var(--text-tertiary);
    }

    &:read-only {
      background-color: var(--bg-secondary);
      cursor: not-allowed;
    }
  }

  select {
    cursor: pointer;
  }

  .zipcode-group {
    display: flex;
    gap: 0.5rem;

    input {
      flex: 1;
    }

    .btn-search {
      padding: 0.75rem 1.5rem;
      background-color: var(--bg-primary);
      color: var(--text-primary);
      border: 1px solid var(--border-color);
      border-radius: 4px;
      font-size: 0.95rem;
      font-weight: 500;
      cursor: pointer;
      white-space: nowrap;
      transition: all 0.2s;

      &:hover {
        background-color: var(--bg-secondary);
        border-color: var(--text-secondary);
      }
    }
  }
}

/* 결제 수단 */
.payment-options {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.payment-option {
  display: flex;
  align-items: start;
  gap: 1rem;
  padding: 1rem;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  background-color: var(--bg-primary);

  &:hover:not(.disabled) {
    border-color: var(--accent-primary);
    background-color: var(--bg-secondary);
  }

  &.selected {
    border-color: var(--accent-primary);
    background-color: var(--bg-secondary);
  }

  &.disabled {
    opacity: 0.5;
    cursor: not-allowed;
    background-color: var(--bg-tertiary);
  }

  input[type="radio"] {
    margin-top: 0.25rem;
    cursor: pointer;

    &:disabled {
      cursor: not-allowed;
    }
  }

  .payment-info {
    flex: 1;

    .payment-header {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-bottom: 0.25rem;

      .payment-icon {
        font-size: 1.25rem;
      }

      .payment-title {
        font-size: 1rem;
        font-weight: 600;
        color: var(--text-primary);
      }
    }

    .payment-description {
      font-size: 0.85rem;
      color: var(--text-secondary);
    }
  }
}

/* 주문 요약 (우측) */
.order-summary {
  background-color: var(--bg-primary);
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 2px 4px var(--shadow);
  position: sticky;
  top: 100px;

  h2 {
    margin: 0 0 1.5rem 0;
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
    padding-bottom: 1rem;
    border-bottom: 2px solid var(--border-color);
  }

  .summary-content {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-bottom: 1.5rem;
  }

  .summary-row {
    display: flex;
    justify-content: space-between;
    font-size: 0.95rem;
    color: var(--text-secondary);

    &.discount {
      color: var(--error-color);
      font-weight: 500;
    }

    &.total {
      font-size: 1.25rem;
      font-weight: 600;
      color: var(--text-primary);
    }
  }

  .summary-divider {
    height: 1px;
    background-color: var(--border-color);
    margin: 0.5rem 0;
  }

  .btn-payment {
    width: 100%;
    padding: 1rem;
    background: var(--gradient-primary);
    color: #fff;
    border: none;
    border-radius: 8px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;

    &:hover:not(:disabled) {
      transform: translateY(-2px);
      box-shadow: 0 4px 8px var(--shadow);
    }

    &:disabled {
      background: var(--bg-tertiary);
      color: var(--text-tertiary);
      cursor: not-allowed;
      opacity: 0.6;
    }
  }

  .payment-notice {
    margin-top: 1rem;
    padding: 1rem;
    background-color: var(--bg-secondary);
    border-radius: 4px;

    p {
      margin: 0;
      font-size: 0.8rem;
      color: var(--text-secondary);
      line-height: 1.5;
    }
  }
}

/* 반응형 */
@media (max-width: 1024px) {
  .checkout-content {
    grid-template-columns: 1fr;
    padding: 1rem;
  }

  .order-summary {
    position: static;
  }
}

@media (max-width: 768px) {
  .checkout-header {
    padding: 1rem;

    h1 {
      font-size: 1.25rem;
    }
  }

  .section {
    padding: 1.5rem;
  }

  .order-item {
    flex-direction: column;

    .item-image {
      width: 100%;
      height: 200px;
    }
  }

  .form-group .zipcode-group {
    flex-direction: column;

    input,
    .btn-search {
      width: 100%;
    }
  }
}
